<template>
    <div>
        <div class="inner-block-custom">
            <label >Operation Calculator</label>
            <div class="p-3" style="width: auto; margin: 20px 10px;">
                <OverlayComponent :show="showOverlay">
                <div>
                    <b-tabs content-class="mt-3" fill>
                        <div>
                            <ModalDialog v-model="isModalOpen" :text="modalText" />
                        </div>
                            <b-tab title="Operations" active>
                                <OperationDisplay :num1="num1" :num2="num2" :operationValue="operationValue" />
                                <OperationButtons :buttons="buttons" @operator-click="handleOperatorClick" />
                                <ResultDisplay :result="result" />
                                <div style="text-align: center; padding: 5%;">
                                    <OverlayComponent :show="showOverlayBtn">
                                    <b-button-group>
                                        <b-button variant="success" :disabled="busy" size="lg" @click="calculate()">Calculate</b-button>
                                        <b-button variant="primary" :class="{ 'disabled': isRandomActive }"
                                            @click="generateRandomNums()">Random Numbers</b-button>
                                    </b-button-group>
                                    </OverlayComponent>
                                </div>
                            </b-tab>
                            <b-tab title="Records">
                                <RecordsTable :items="items" :fields="fields" :perPage="perPage" :currentPage="currentPage"
                                    :sortBy.sync="sortBy" :sortDesc.sync="sortDesc" :totalRecords="totalRecords"/>
                            </b-tab>
                        </b-tabs>
                    </div>
                </OverlayComponent>
            </div>
        </div>
    </div>
</template>
<script>

import logic from "@/assets/js/logic";
import ModalDialog from "@/components/ModalDialog";
import OperationDisplay from "@/components/OperationDisplay";
import OperationButtons from "@/components/OperationButtons";
import ResultDisplay from "@/components/ResultDisplay";
import RecordsTable from "@/components/RecordsTable";
import OverlayComponent from "@/components/Overlay.vue";

export default {
    components: {
        ModalDialog,
        OperationDisplay,
        OperationButtons,
        ResultDisplay,
        RecordsTable,
        OverlayComponent,
    },
    created() {
        this.fetchData();
    },
    data() {
        return {
            perPage: 6,
            busy: false,
            currentPage: 1,
            items: [],
            totalRecords: null,
            showOverlay: false,
            showOverlayBtn: false,
            fields: [
                { key: "id", label: "ID" },
                { key: "operation.type", label: "Op. type" },
                { key: "operation.cost", label: "Cost" },
                { key: "operation_response", label: "Op .Result" },
                { key: "date", label: "Op. date" },
                { key: "delete", label: "" },
            ],
            buttons: [],
            sortBy: "",
            sortDesc: false,
            num1: null,
            num2: null,
            isModalOpen: false,
            modalText: "",
            isRandomActive: false,
            filterValue: "",
            operationValue: "",
            result: null,
        };
    },
    methods: {
        async fetchData() {
            this.showOverlay = !this.showOverlay
            await Promise.all([this.generateRandomNums()]);
            this.fetchButtons();
            this.showOverlay = !this.showOverlay
        },
        async fetchButtons() {
            try {
                const response = await logic.getAvOperations();
                this.buttons = response.data.data;
            } catch (error) {
                this.openModal(error.message);
            }
        },
        handleOperatorClick(operator, opId) {
            if (operator === "RDM") {
                this.num1 = null;
                this.num2 = null;
                this.isRandomActive = true;
            } else if (operator === "√") {
                this.num2 = null;
                this.isRandomActive = false;
            } else {
                this.isRandomActive = false;
            }
            this.operationValue = operator;
            this.operation_id = opId;
        },
        async generateRandomNums() {
            this.showOverlayBtn = !this.showOverlayBtn;
            try {
                const response = await logic.getRandomNumbers("numeric=true");
                const [num1, num2] = response.data.data;
                if (this.operationValue === "√") {
                    this.num1 = num1;
                    this.num2 = null;
                }else{
                    this.num1 = num1;
                    this.num2 = num2;
                }
            } catch (error) {
                this.openModal(error.message);
            }
            this.showOverlayBtn = !this.showOverlayBtn;
        },
        async calculate() {
            if ((this.num1 && this.num2) === null && this.operationValue !== "RDM") {
                this.openModal("Please generate random value(s) to proceed with the operation.");
            } else if (this.operationValue === "") {
                this.openModal("Please select a valid operation.");
            } else {
                this.showOverlayBtn = !this.showOverlayBtn;
                try {
                    if (this.operationValue === "RDM") {
                        const response = await logic.getRandomNumbers("numeric=false");
                        this.string = response.data.data[0];
                        const result = await logic.OperationCalculation(this.string, this.num2, this.operation_id);
                        this.result = result.data.data['operation_response'];
                    } else {
                        const result = await logic.OperationCalculation(this.num1, this.num2, this.operation_id);
                        console.log(result);
                        this.result = result.data.data['operation_response'];
                    }
                } catch (error) {
                    this.openModal(error.message);
                }
                this.showOverlayBtn = !this.showOverlayBtn;
            }
        },
        openModal(msg) {
            this.isModalOpen = true;
            this.modalText = msg;
        },
    }
};
</script>
