<template>
    <div class="overflow-auto">
        <div class="mb-2">
            <b-form-input v-model="filterValue" placeholder="Filter"></b-form-input>
        </div>
        <OverlayComponent :show="showOverlayTable">
            <b-table id="my-table" :items="items" :fields="fields" :per-page="perPage"
                small :sort-by.sync="sortBy" :sort-desc.sync="sortDesc" @row-clicked="rowClicked">
                <template #cell(delete)="row">
                    <b-button variant="danger" size="sm" @click="deleteRow(row.item)">
                        Remove
                    </b-button>
                </template> 
            </b-table>
        </OverlayComponent>
        <b-pagination v-model="currentPage" align="center" :per-page="perPage" :total-rows="totalRecords"
            aria-controls="my-table" @input="handlePageChange"></b-pagination>
    </div>
</template>

<script>

import logic from "@/assets/js/logic";
import ModalDialog from "@/components/ModalDialog";
import OverlayComponent from "@/components/Overlay.vue";

export default {
    components:{
        ModalDialog,
        OverlayComponent,
    },
    created() {
        this.fetchData();
    },
    data() {
        return {
            perPage: 6,
            currentPage: 1,
            items: [],
            totalRecords: null,
            showOverlayTable: false,
            fields: [
                { key: "id", label: "ID" },
                { key: "operation.type", label: "Op. type" },
                { key: "operation.cost", label: "Cost" },
                { key: "operation_response", label: "Op .Result" },
                { key: "date", label: "Op. date" },
                { key: "delete", label: "" },
            ],
            sortBy: "",
            sortDesc: false,
            filterValue: "",
            operationValue: ""
        };
    },
    
    computed: {
        filteredItems() {
            if (!this.filterValue) {
                return this.items;
            }
            const regex = new RegExp(this.filterValue, 'i');
            return this.items.filter((item) => regex.test(item.name));
        },
        totalItems() {
            return this.totalRecords;
        },
        paginatedItems() {
            const start = (this.currentPage - 1) * this.perPage;
            const end = start + this.perPage;
            return this.filteredItems.slice(start, end);
        },
    },
    methods: {
        async fetchData() {
            await Promise.all([this.fetchRecords(1)]);
        },
        async fetchRecords(newPage) {
            this.showOverlayTable = !this.showOverlayTable
            try {
                const response = await logic.getRecords(null, newPage, this.perPage);
                this.totalRecords = response.data.total_records;
                this.items = response.data.data;
            } catch (error) {
                this.openModal(error.message);
            }
            this.showOverlayTable = !this.showOverlayTable
        },
        async deleteRow(item) {
            try {
                await logic.removeRecord(item.id);
                const index = this.items.indexOf(item);
                if (index == -1) {
                    this.items.splice(index, 1);
                }
                this.fetchRecords(this.currentPage);
            } catch (error) {
                this.openModal(error.message);
            }
        },
        rowClicked(item) {
            //code
        },
        handlePageChange(newPage) {
            this.fetchRecords(newPage);
            this.$emit('update:current-page', newPage);
        },
    },
};
</script>
